import React from "react";
import styles from "./product.module.css";
import { useItemState } from "../context";

function ProductItem({ item }) {
    return (
        <div>
            <div>
                <span></span>
            </div>
            <img src={item.src} alt=" " />
            <h4>{item.title}</h4>
            <p>{item.price}</p>
        </div>
    )
}

function Product() {
    const items = useItemState()
    return (
        <div>
            <div className="subimg">
                <div className="subimg_in">
                    <p>Home &#62; </p>
                    <h2></h2>
                </div>
                <div style={{ marginTop: '100px', float: 'left', width: '100%' }}>
                    <p style={{ float: 'left', width: '100%' }}> <span style={{ color: 'red' }}>26</span>  .
                        <button style={{ float: 'right', border: '1px solid #000', backgroundColor: '#fff', padding: '10px', boxSizing: 'border-box' }}></button></p>
                </div>
            </div>
            <div className="contents">
                <div className="contents_in">
                    <div className={styles.list_h}>
                        <ul>
                            <li></li>
                            <li></li>
                            <li></li>
                            <li></li>
                            <li></li>
                        </ul>
                        <div>200 </div>
                    </div>
                    <div className={styles.list_b}>
                        {items.map((item) => (
                            <ProductItem item={item} key={item.id} />
                        ))}
                    </div>
                </div>
            </div>
        </div >
    )
}
export default Product;