import React from "react";
import styles from './member.module.css';
import { useItemState } from "../context";

function Memberitem({ item }) {
    if (item.id <= 4) {
        return (
            <div>
                <div>
                    <span></span>
                </div>
                <img src={item.src} alt=" " />
                <h4>{item.title}</h4>
                <p>{item.price}</p>
            </div>
        )
    }
}

